import React from 'react';
import './main.css';
import todos from '../../data/dummyTodos';

const Main = () => {
  return (
    <div className="container">
      <img className="image" width='300' height='300' src={'http://192.168.1.198:8081/image'} alt='dailypic' />
      <div className="rowContainer">
        <input type="text" maxLength="140" placeholder="new todo" />
        <div className="button">
          <button>create todo</button>
        </div>
      </div>
      <div className="todoList">
        {
          todos.map(todo => (
            <li id={todo.id}>{todo.todo}</li>
          ))
        }
      </div>
    </div>
  )

};

export default Main;